.menu {
  background-color: $bgc;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $headerTopPadding 0;

    @include onLaptop {
      padding: 52px 0;
    }

    @include onDesctop {
      padding: 52px 0;
    }
  }

  &__top {
    margin-bottom: 55px;

    @include onDesctop {
      margin-bottom: 80px;
    }

    @include topAction;
  }

  &__list {
    margin: 0 0 40px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    text-decoration: none;
    font-size: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $main-color;
    transition: border-color 0.3s;

    &:hover {
      border-color: $main-color;
    }
  }

  &__call {
    display: block;
    width: max-content;
    text-decoration: none;
    color: $main-color;
    text-transform: uppercase;

    &--phone {
      font-size: 16px;
      line-height: 27px;
      margin-bottom: 16px;
    }

    &--action {
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
      padding-bottom: 8px;
      border-bottom: 1px solid $main-color;
    }
  }
}
