.contacts {
  &__info {
    margin: 0 0 24px;
  }

  &__info-title {
    margin: 0 0 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #9fadc2;
  }

  &__info-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #3e3e3e;
    text-decoration: none;
    transition: color 0.3s;

    &:hover {
      color: rgb(46, 85, 170);
    }
  }

  &__form {
    margin-bottom: 48px;

    @include onLaptop {
      margin-bottom: 0;
    }
  }
  
  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }
}
